<template>
    <d2-container>
        <!-- 供应商管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="供应商管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_val">添加</el-button>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="master_name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="supplier_name" label="供应商名称" width="180">
            </el-table-column>
            <el-table-column prop="supplier_address" label="地址">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_val(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="emit_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <!-- <el-checkbox v-model="checked" style="margin: 0 10px;"></el-checkbox>
                <el-button type="danger">批量删除</el-button> -->
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加供应商 -->
        <el-dialog :title="num" :visible.sync="dialogVisible" width="30%" :before-close="clear">
            <el-input placeholder="请输入供应商名称..." v-model="form.supplier_name" style="margin-top: 10px;">
                <template slot="prepend">供应商名称</template>
            </el-input>
            <el-input placeholder="请输入联系人姓名..." v-model="form.master_name" style="margin-top: 10px;">
                <template slot="prepend">联系人姓名</template>
            </el-input>
            <el-input placeholder="请输入联系人电话..." v-model="form.master_phone" style="margin-top: 10px;">
                <template slot="prepend">联系人电话</template>
            </el-input>
            <el-input placeholder="请输入供应商地址..." v-model="form.supplier_address" style="margin-top: 10px;">
                <template slot="prepend">供应商地址</template>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onsubmit" v-if="!form.id">确 定</el-button>
                <el-button type="primary" @click="onsubmit_edit" v-if="form.id">确 定</el-button>
            </span>
        </el-dialog>
    </d2-container>
</template>

<script>
    import { supplier, add_supplier, del_supplier, edit_supplier } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'manage_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                dialogVisible: false,
                checked: 1,
                pages:1,
                multipleSelection: [],
                tableData: [],
                form: {
                    supplier_name: '',
                    master_name: '',
                    master_phone: '',
                    supplier_address: ''
                }
            }
        },
        methods: {
            handleSelect(item) {
                console.log(item);
            },
            page(a){
                console.log(a);
                this.pages = a;
                this.initsupplier()
            },
            add_val() {//添加供应商
                this.num = '添加供应商'
                this.dialogVisible = true;
            },
            onsubmit() {//添加供应商
                // console.log(this.form);
                let att = qs.stringify(this.form)
                add_supplier(att).then(res => {
                    console.log(res);
                    if (res.code) {
                        this.$message({
                            message: res.message,
                            center: true,
                            type: 'success'
                        });
                        this.clear()
                        this.initsupplier()
                        return
                    }
                    Myerror(res, this)
                })
            },
            emit_val(a) {//删除
                console.log(a);
                let arr = a.id;
                del_supplier(arr).then(res => {
                    console.log(res);
                    if (res.code) {
                        this.$message({
                            message: res.message,
                            center: true,
                            type: 'success'
                        });
                        this.initsupplier()
                        return
                    }
                    Myerror(res, this)
                })

            },
            edit_val(a) {//修改供应商
                this.num = '修改供应商';
                this.form = {
                    supplier_name: a.supplier_name,
                    master_name: a.master_name,
                    master_phone: a.master_phone,
                    supplier_address: a.supplier_address,
                    id: a.id
                }
                this.dialogVisible = true;
            },
            onsubmit_edit() {//提交修改
                let par = {
                    supplier_name: this.form.supplier_name,
                    master_name: this.form.master_name,
                    master_phone: this.form.master_phone,
                    supplier_address: this.form.supplier_address,
                }
                let id = this.form.id
                edit_supplier(par, id).then(res => {
                    console.log(res);
                    if (res.code) {
                        this.$message({
                            message: res.message,
                            center: true,
                            type: 'success'
                        });
                        this.initsupplier()
                        this.clear()
                        return
                    }
                    Myerror(res, this)
                })
            },
            initsupplier() {
                let par = {
                    page:this.pages,
                    limit:10
                }
                supplier(par).then(res => {
                    console.log(res);
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total
                })
            },
            clearfrom() {
                this.form = {
                    supplier_name: '',
                    master_name: '',
                    master_phone: '',
                    supplier_address: ''
                }
            },
            clear() {
                this.dialogVisible = false;
                this.clearfrom()
            }
        },
        mounted() {
            this.initsupplier()
        }
    }
</script>
<style lang="scss">
</style>